<template>
  <div class="container pt-5">
    <div class="row mb-3">
      <div class="col">
        <h1>wine.ru</h1>
      </div>
    </div>
    <div class="row">
      <div class="col-md-4">
        <div class="form-group">
          <label for="search" class="mb-2">Поиск вина</label>
          <input
            v-model.trim="search"
            type="text"
            class="form-control"
            id="search"
            placeholder="Винодельня, вино..."
          />
        </div>
        <hr />

        <ul class="list-group">
          <li
            v-for="(wine, index) of filterWine"
            :key="wine.id"
            @click="selectWine(index)"
            :class="{active: selectedWineIndex === index}"
            class="list-group-item list-group-item-action pointer"
          >
            {{ wine.winery + " - " + wine.wine }}
          </li>
        </ul>
      </div>
      <div class="col-md-8">
        <div class="row">
          <div class="col-md-5">
            <h2>{{ dWine.winery }}</h2>
            <img
              :src="dWine.image"
              :alt="dWine.winery"
              class="rounded wineImage"
            />
          </div>
          <div class="col-md-7 mt-3">
            <h5>Подробнее:</h5>

            <ul>
              <li>
                {{ dWine.winery }} - <strong>{{ dWine.wine }}</strong>
              </li>
              <li>
                Рейтинг - <strong>{{ dWine.rating.average }}</strong>
              </li>
              <li>
                География - <strong>{{ dWine.location }}</strong>
              </li>
            </ul>

            <p v-if="phoneVis">{{ dWine.wine }}</p>

            <button class="btn btn-primary">Купить</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
let wines = [
  {
    winery: "Maselva",
    wine: "Emporda 2012",
    rating: {
      average: "4.9",
      reviews: "88 ratings",
    },
    location: "Spain\n·\nEmpordà",
    image:
      "https://images.vivino.com/thumbs/ApnIiXjcT5Kc33OHgNb9dA_375x500.jpg",
    id: 1,
  },
];

export default {
  data() {
    return {
      dWines: [],
      dWine: wines[0],
      selectedWineIndex: 0,
      phoneVis: false,
      search: "",
    };
  },
  methods: { 
    selectWine(index) {
      this.dWine = this.filterWine[index];
      this.selectedWineIndex = index
      window.scrollTo(0,0);
    },
    // changeRender() {
    //   this.selectedWineIndex = 0
    //   }
    
  },
  computed: {
    filterWine() { 
  //  this.changeRender()
   let searchTemplate = this.search.toLowerCase();
   return this.dWines.filter((wine) => {
    return wine.wine.toLowerCase().includes(searchTemplate)  || wine.winery.toLowerCase().includes(searchTemplate) || wine.location.toLowerCase().includes(searchTemplate)
   })
    },
  },
  watch: {
    filterWine(){
      this.selectedWineIndex = 0
      if(this.filterWine[0] != undefined){
      this.dWine = this.filterWine[0]}
    }
  },
  created() {
    axios
      .get("https://api.sampleapis.com/wines/reds")
      .then((response) => (this.dWines = response.data))
      .catch((error)=>console.log(error));
  },
};
</script>

<style>
.pointer {
  cursor: pointer;
}
.wineImage {
  /* width: 100%; */
  height: 400px;
  object-fit: cover;
}
</style>